export default function AboutMe() {
    return (
      <section id="AboutMe" className="about--section">
        <div className="about--section--img">
          <img src="./img/about-me.png" alt="About Me" />
        </div>
        <div className="hero--section--content--box about--section--box">
          <div className="hero--section--content">
            <p className="section--title">About</p>
            <h1 className="skills-section--heading">About Me</h1>
            <p className="hero--section-description">
              Hello! I'm an aspiring Electrical Engineer with a passion for designing and implementing electrical systems. 
            </p>
            <p className="hero--section-description">
               I have deep hands-on experience with designing and implementing electrical systems through my personal and university projects.
               I always look forward to working with others and taking on challenges to further my growth.
            </p>
          </div>
        </div>
      </section>
    );
  }